import React, { useState, useEffect } from "react";
import axios from "axios";
import { Layout, Breadcrumb, PageHeader, Table, Button } from "antd";
import { PieChartOutlined } from "@ant-design/icons";
const { Header, Content } = Layout;

import MainLayout from "../_components/MainLayout";
import AddClassModal from "../_components/ClassPage/addClass";
const detailPage = {
    title: "Class",
    navSelected: {
        main: "class",
        sub: ""
    }
};

const columns = [
    {
        title: "Name",
        dataIndex: "name",
        sorter: true,
        render: name => `${name.first} ${name.last}`,
        width: "20%"
    },
    {
        title: "Gender",
        dataIndex: "gender",
        filters: [
            { text: "Male", value: "male" },
            { text: "Female", value: "female" }
        ],
        width: "20%"
    },
    {
        title: "Email",
        dataIndex: "email"
    }
];

const ClassPage = () => {
    return (
        <MainLayout navSelected={detailPage.navSelected}>
            <Header className="site-layout-background" style={Style.Header}>
                <PageHeader
                    className="site-page-header"
                    title={detailPage.title}
                />
            </Header>
            <Content style={{ margin: "0 16px" }}>
                <Breadcrumb style={{ margin: "16px 0" }}>
                    <Breadcrumb.Item>
                        <PieChartOutlined /> Dashboard
                    </Breadcrumb.Item>
                    <Breadcrumb.Item>{detailPage.title}</Breadcrumb.Item>
                </Breadcrumb>
                <div className="site-layout-background" style={Style.Content}>
                    <AddClassModal />
                </div>
            </Content>
        </MainLayout>
    );
};
export default ClassPage;

const Style = {
    Header: {
        background: "#fff",
        padding: 0
    },
    Content: {
        background: "#fff",
        padding: 24,
        minHeight: 360
    }
};
